<style lang="less">
    #project {
        height: 1500px;

        h1 {
            font-size: 30px;
            text-align: center;
            line-height: 500px;

        }
    }


    #project {
        /* 正文开始 */

        main {
            background-color: #fff;

            > div {
                display: flex;
                justify-content: space-between;
                padding-bottom: 212px;
            }

            .m-left {
                flex: 0 0 872px;
                /* 问题表单 */

                .ml-tab {
                    border-bottom: 2px solid #f1f1f1;
                    padding-bottom: 6px;

                    li {
                        float: left;
                        font-size: 18px;
                        color: #4f646a;
                        margin-left: 22px;
                        height: 32px;
                        position: relative;
                        cursor: pointer;
                        line-height: 37px;

                        &.mlt-active {
                            color: #1e2629;
                            font-size: 22px;

                            &::after {
                                content: "";
                                position: absolute;
                                bottom: -7px;
                                left: 0;
                                background-color: #e8332f;
                                width: 132px;
                                height: 4px;
                                border-radius: 2px;
                            }
                        }
                    }
                }

                .ml-item {
                    ul {
                        padding-top: 21px;

                        li {
                            cursor: pointer;
                            width: 100%;
                            //background-color: pink;
                            margin-bottom: 28px;
                            padding-top: 21px;
                            border-bottom: 1px solid #f1f1f1;
                            padding-bottom: 29px;

                            .nq-item {
                                width: 100%;
                                height: 226px;
                                padding-top: 30px;
                                padding-left: 23px;
                                padding-right: 17px;
                                position: relative;
                                background-color: #fff;
                                z-index: 2;
                                display: flex;
                                justify-content: space-between;

                                img {
                                    display: block;
                                    flex: 0 0 259px;
                                    height: 168px;
                                }

                                .news_text {
                                    flex: 0 0 544px;

                                    h2 {
                                        font-size: 18px;
                                        color: #1e2629;
                                        display: inline-block;
                                    }

                                    .price {
                                        float: right;
                                        font-size: 12px;
                                        color: #4f646a;

                                        i {
                                            font-size: 16px;
                                            color: #ff1734;
                                        }
                                    }

                                    .nt-box {
                                        margin-top: 10px;
                                        margin-bottom: 10px;

                                        span {
                                            border-radius: 11px;
                                            line-height: 21px;
                                            text-align: center;
                                            color: #fff;
                                            padding: 0 11px 0 9px;
                                            font-size: 14px;
                                            margin-right: 15px;


                                            &:nth-of-type(1) {
                                                background-color: #e8448c;
                                            }

                                            &:nth-of-type(2) {
                                                background-color: #4577bc;
                                            }
                                        }
                                    }

                                    .introduce {
                                        color: #4f646a;
                                        font-size: 16px;
                                    }

                                    .introduce-info {
                                        color: #4f646a;
                                        font-size: 16px;
                                        overflow: hidden;
                                        -webkit-line-clamp: 2;
                                        text-overflow: ellipsis;
                                        display: -webkit-box;
                                        -webkit-box-orient: vertical;
                                    }

                                    .nq-bot-info {
                                        margin-top: 11px;

                                        .pernum {
                                            height: 26px;
                                            float: left;

                                            span {
                                                color: #1e2629;
                                                margin-right: 10px;
                                                line-height: 26px;
                                            }
                                        }

                                        .btns {
                                            float: right;

                                            a {
                                                display: inline-block;
                                                width: 72px;
                                                height: 26px;
                                                line-height: 26px;
                                                color: #fff;
                                                text-align: center;
                                                border-radius: 5px;
                                                font-size: 14px;
                                                margin-right: 10px;

                                                &:nth-of-type(1) {
                                                    background-color: #f18d2f;
                                                }

                                                &:nth-of-type(2) {
                                                    background-color: #e8332f;
                                                }
                                            }
                                        }
                                    }


